* {
  box-sizing: border-box;
}

body {
  background: #e7efec;
  color: #2f2f2f;
  margin: 0;
  text-align: left;
}

button,
input {
  outline: none;
}

a {
  text-decoration: none;
}

.flights__container {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}

.search-field {
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto;
}
.search-field__title {
  font-weight: 300;
  font-size: 35px;
  line-height: 40px;
  margin-bottom: 0.5rem;
}
.search-field__search-line {
  overflow: hidden;
  margin-top: 24px;
  height: 50px;
  display: flex;
}
.search-field__input {
  font-size: 1.5rem;
  width: 75%;
  padding-left: 1.5rem;
  padding-right: 1rem;
  border: none;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.search-field__button {
  text-transform: uppercase;
  border: none;
  font-weight: 700;
  width: 25%;
  background: #1eb7ee;
  color: #fff;
  font-size: 1.25rem;
  text-align: center;
  cursor: pointer;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}

.flights-tabs {
  width: 100%;
  margin: 24px auto 0;
}
.flights-tabs__tab {
  display: inline-block;
  width: 50%;
  text-align: center;
  outline: none;
  font-size: 24px;
  line-height: 60px;
  text-transform: uppercase;
  font-weight: 600;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  color: #fff;
  background: #73d2f0;
  /* overflow: hidden; */
}

.flights-tabs__tab_active {
  color: #1eb7ee;
  background: #fff;
}

.flights-dates__container {
  display: flex;
  padding: 10px 0 15px;
  background: #fff;
}

.flights-calendar__container {
  border: none !important;
  height: auto;
  line-height: inherit !important;
  width: 40px;
  padding-top: 15px;
  margin-left: 20px;
  position: relative;
}

.flights-calendar__container::after {
  content: '';
  height: 40px;
  width: 40px;
  display: block;
  background: #1eb7ee url(https://iev.aero/images/icons/calendar.svg) 50%
    no-repeat;
  background-size: 70%;
}

.flights-calendar__datepicker {
  position: static;
  text-align: left;
  left: 0;
  height: 100%;
  border: 0;
}

.flights-calendar__input {
  padding: 0;
  font-size: 12px;
  font-weight: 700;
  color: #1eb7ee;
  position: absolute;
  top: 0;
  height: auto;
  text-align: center;
  padding-bottom: 40px;
  background: 0 0;
  border: none;
  width: 100%;
  cursor: pointer;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  outline: none;
  overflow: visible;
}

.flights-dates__list {
  display: flex;
  margin-bottom: 4px;
  margin-left: 1.5rem;
  width: 300px;
}
.flights-dates__day {
  display: flex;
  flex-direction: column;
  width: 33%;
  justify-content: space-between;
  border-bottom: 3px solid #d0d0d0;
  padding-bottom: 7px;
  text-align: center;
  cursor: pointer;
  color: #b8b9b9;
  font-weight: 700;
  text-transform: uppercase;
}
.flights-dates__day span {
  font-size: 12px;
  font-weight: normal;
  padding-top: 0px;
}

.flights-dates__day_active {
  color: #1eb7ee;
  border-color: #1eb7ee;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.flights-table {
  width: 100%;
  color: #212529;
  background-color: #fff;
  margin-bottom: 2rem;
}

.flights-table td,
.flights-table th {
  padding: 0.75rem;
}

.flights-table thead {
  color: #95989a;
  font-size: 1rem;
  font-weight: 600;
}

.flights-table thead th {
  vertical-align: bottom;
}

.flights-table thead th:first-of-type {
  text-align: center;
}

.flights-table tbody tr:nth-of-type(odd) {
  background-color: rgba(0, 102, 135, 0.1);
}
.flights-table tbody tr:hover {
  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
.flights-table td {
  font-size: 18px;
  line-height: 40px;
  padding: 16px 10px;
  border: 0;
  vertical-align: top;
}

.flights-table__terminal-col {
  text-align: center;
}
.flights-table__terminal-col span {
  color: #63c745;
  font-weight: 700;
  border: 1px solid #63c745;
  border-radius: 50%;
  display: inline-block;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 35px;
  font-size: 22px;
}
.flights-table__terminal-col.blue span {
  border-color: #1eb7ee;
  color: #1eb7ee;
}

.nothing-found {
  min-height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 30px;
  font-weight: 700;
  padding: 0 50px;
  background-color: #fff;
}

.react-datepicker .react-datepicker__day {
  border: 1px solid transparent;
}

.react-datepicker .react-datepicker__day:hover {
  border-radius: 0;
  border: 1px solid #4bd;
  background-color: transparent;
}
.react-datepicker .react-datepicker__day--selected {
  border-radius: 0;
  background-color: #44bbdd;
}
.react-datepicker .react-datepicker__day--selected:hover {
  background-color: #44bbdd;
}
.react-datepicker .react-datepicker__day:focus {
  outline: none;
}
.react-datepicker__day--keyboard-selected {
  border-radius: 0;
  background-color: transparent;
  color: #000;
}
.react-datepicker__header {
  border-bottom: none;
}

@media (min-width: 768px) {
  .search-field {
    max-width: 83.33333%;
  }
  .search-field__title {
    line-height: 80px;
    font-size: 60px;
  }
  .search-field__search-line {
    height: 60px;
    font-size: 18px;
  }
}
@media (min-width: 992px) {
  .flights__container {
    max-width: 100%;
    padding: 0;
  }
  .search-field__title {
    font-size: 70px;
  }
  .search-field__search-line {
    height: 80px;
    font-size: 24px;
  }
  .search-field__button {
    font-size: 26px;
  }
}
@media (min-width: 1200px) {
  .flights__container {
    max-width: 1140px;
  }
  .search-field {
    max-width: 66.66667%;
  }
}
